
import {
  computed,
  defineComponent,
  nextTick,
  onMounted,
  ref,
  watch,
  defineAsyncComponent,
} from "vue";
import { ComponentType } from "@/components/Editor/RenderComponent/types";

export default defineComponent({
  inheritAttrs: false,
  name: ComponentType.Tab,
  props: {
    children: {
      type: Array,
      required: true,
    },
    active: {
      type: Number,
      required: true,
    },
  },
  components: {
    componentWrapper: defineAsyncComponent((): any => {
      return process.env.NODE_ENV !== "production"
        ? import(
            "@/components/Editor/RenderComponent/ComponentWrapper/index.vue"
          )
        : import("@/components/Previewer/render.vue");
    }),
  },
  setup(props) {
    const privateActive = ref(props.active);
    const tabTitleRefs = ref([]);
    const transform = ref("");
    const getTransform = async () => {
      await nextTick();
      const $ref = tabTitleRefs.value[privateActive.value] as HTMLElement;
      const { offsetLeft, offsetWidth } = $ref;
      transform.value = `translateX(${
        offsetLeft + offsetWidth / 2
      }px) translateX(-50%)`;
    };
    watch(privateActive, getTransform);
    const tabsLength = computed(() => {
      return props.children.length;
    });
    watch(tabsLength, () => {
      privateActive.value =
        privateActive.value > tabsLength.value - 1
          ? tabsLength.value - 1
          : privateActive.value;
    });

    onMounted(() => {
      getTransform();
    });
    return {
      transform,
      privateActive,
      tabTitleRefs,
      change(index: number) {
        privateActive.value = index;
      },
    };
  },
});
